                                                                                                                                                                                                                                                                                                                                                                                            header {
                                                                                                                                                                                                                                                                                                                                                                                                height: 100vh;
                                                                                                                                                                                                                                                                                                                                                                                                padding-top: 3rem;
                                                                                                                                                                                                                                                                                                                                                                                                overflow: hidden;
                                                                                                                                                                                                                                                                                                                                                                                                text-align: center;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .header__container {
                                                                                                                                                                                                                                                                                                                                                                                                text-align: center;
                                                                                                                                                                                                                                                                                                                                                                                                height: 100%;
                                                                                                                                                                                                                                                                                                                                                                                                position: relative;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .cta {
                                                                                                                                                                                                                                                                                                                                                                                                display: flex;
                                                                                                                                                                                                                                                                                                                                                                                                gap: 1px;
                                                                                                                                                                                                                                                                                                                                                                                                margin-top: 2rem;
                                                                                                                                                                                                                                                                                                                                                                                                justify-content: center;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .header__socials {
                                                                                                                                                                                                                                                                                                                                                                                                display: flex;
                                                                                                                                                                                                                                                                                                                                                                                                flex-direction: column;
                                                                                                                                                                                                                                                                                                                                                                                                align-items: center;
                                                                                                                                                                                                                                                                                                                                                                                                gap: 0.2rem;
                                                                                                                                                                                                                                                                                                                                                                                                position: absolute;
                                                                                                                                                                                                                                                                                                                                                                                                left: 0;
                                                                                                                                                                                                                                                                                                                                                                                                bottom: 2rem;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .header__socials::after {
                                                                                                                                                                                                                                                                                                                                                                                                content: '';
                                                                                                                                                                                                                                                                                                                                                                                                width: 1px;
                                                                                                                                                                                                                                                                                                                                                                                                height: 2rem;
                                                                                                                                                                                                                                                                                                                                                                                                background-color: var(--color-primary);
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .me {
                                                                                                                                                                                                                                                                                                                                                                                                background: linear-gradient(var(--color-primary), transparent);
                                                                                                                                                                                                                                                                                                                                                                                                width: 30rem;
                                                                                                                                                                                                                                                                                                                                                                                                height: 45rem;
                                                                                                                                                                                                                                                                                                                                                                                                position: absolute;
                                                                                                                                                                                                                                                                                                                                                                                                left: calc(35%);
                                                                                                                                                                                                                                                                                                                                                                                                margin-top: 1rem;
                                                                                                                                                                                                                                                                                                                                                                                                border-radius: 12rem 12rem 0 0;
                                                                                                                                                                                                                                                                                                                                                                                                overflow: hidden;
                                                                                                                                                                                                                                                                                                                                                                                                padding: 5rem 1.5rem 1.5 rem 1.5rem;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .scroll_down {
                                                                                                                                                                                                                                                                                                                                                                                                position: absolute;
                                                                                                                                                                                                                                                                                                                                                                                                right: -2.3rem;
                                                                                                                                                                                                                                                                                                                                                                                                bottom: 5rem;
                                                                                                                                                                                                                                                                                                                                                                                                transform: rotate(90deg);
                                                                                                                                                                                                                                                                                                                                                                                                font-weight: 200;
                                                                                                                                                                                                                                                                                                                                                                                                font-size: 0.9rem;
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            @media screen and (max-width: 1024px) {
                                                                                                                                                                                                                                                                                                                                                                                                header {
                                                                                                                                                                                                                                                                                                                                                                                                    height: 68vh;
                                                                                                                                                                                                                                                                                                                                                                                                }
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            @media screen and (max-width: 600px) {
                                                                                                                                                                                                                                                                                                                                                                                                header {
                                                                                                                                                                                                                                                                                                                                                                                                    height: 100vh;
                                                                                                                                                                                                                                                                                                                                                                                                }
                                                                                                                                                                                                                                                                                                                                                                                                .header__socials,
                                                                                                                                                                                                                                                                                                                                                                                                .scroll_down {
                                                                                                                                                                                                                                                                                                                                                                                                    display: none;
                                                                                                                                                                                                                                                                                                                                                                                                }
                                                                                                                                                                                                                                                                                                                                                                                            }
                                                                                                                                                                                                                                                                                                                                                                                            
                                                                                                                                                                                                                                                                                                                                                                                            .container.header__container,
                                                                                                                                                                                                                                                                                                                                                                                            h1 {
                                                                                                                                                                                                                                                                                                                                                                                                font-style: oblique 40deg;
                                                                                                                                                                                                                                                                                                                                                                                                color: aqua;
                                                                                                                                                                                                                                                                                                                                                                                            }