                                                                                                                                                                                                    #testimonials {
                                                                                                                                                                                                        margin-top: 20%;
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    .container.testimonials__container {
                                                                                                                                                                                                        width: 40%;
                                                                                                                                                                                                        margin-top: 80px;
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    .client__avatar {
                                                                                                                                                                                                        width: 4rem;
                                                                                                                                                                                                        aspect-ratio: 1/1;
                                                                                                                                                                                                        overflow: hidden;
                                                                                                                                                                                                        border-radius: 50%;
                                                                                                                                                                                                        margin: 0 auto 1rem;
                                                                                                                                                                                                        border: 0.4rem solid var(--color-primary-variant);
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    .testimonial {
                                                                                                                                                                                                        background: var(--color-bg-variant);
                                                                                                                                                                                                        text-align: center;
                                                                                                                                                                                                        padding: 2rem;
                                                                                                                                                                                                        border-radius: 2rem;
                                                                                                                                                                                                        user-select: none;
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    .client__review {
                                                                                                                                                                                                        color: var(--color-light);
                                                                                                                                                                                                        font-weight: 300;
                                                                                                                                                                                                        display: block;
                                                                                                                                                                                                        width: 80%;
                                                                                                                                                                                                        margin: 0.8 rem auto 0;
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    @media screen and (max-width: 1024px) {
                                                                                                                                                                                                        .container.testimonials__container {
                                                                                                                                                                                                            width: 60%;
                                                                                                                                                                                                        }
                                                                                                                                                                                                    }
                                                                                                                                                                                                    
                                                                                                                                                                                                    @media screen and (max-width: 600px) {
                                                                                                                                                                                                        .container.testimonials__container {
                                                                                                                                                                                                            width: var(--container-width-sm);
                                                                                                                                                                                                        }
                                                                                                                                                                                                        .client__review {
                                                                                                                                                                                                            width: var(--container-width-sm);
                                                                                                                                                                                                        }
                                                                                                                                                                                                    }